const Refresher = () => {
  const refreshCurrentDepositQuery =
    useRecoilRefresher_UNSTABLE(currentDepositQuery);

  const initialDepositObj = useRecoilValue(currentDepositQuery);
  const [depositStatus, setDepositStatus] = useState(initialDepositObj?.status);
  const token = useRecoilValue(tokenState);

  //
  const refresh = async () => {
    console.log('refreshing');
    console.log(`HM ${new Date().getTime() - updatedAt.getTime()}`);
    if (new Date().getTime() - updatedAt.getTime() > REFRESH_PERIOD) {
      const nextDeposit = await authRequest(
        '/current_deposit',
        token as string
      );
      const state = nextDeposit?.data?.status;
      console.log(`state: ${state}`);
      switch (state) {
        case 'CANCELED':
          refreshCurrentDepositQuery();
          break;
        case 'PAID':
          console.log('PAID');
          updatedAt = new Date();
          setTimeout(refresh, REFRESH_PERIOD);
          break;
        default:
          setDepositStatus('EXECUTED');
      }
    }
  };

  useEffect(() => {
    updatedAt = subDays(new Date(), 1);
    refresh();
  }, []);

  const { cancelDeposit } = useDepositActions();
  const doCancelDeposit = (e: React.MouseEvent<HTMLElement>) => {
    e.preventDefault();
    cancelDeposit();
  };
  return (
    <div className="confirmation">
      <div className="lk-step__title lk__title">
        <span>Confirmation</span>
      </div>
      <div className="lk-currency__content">
        <div className="confirmation-inner">
          <div className="confirmation__icon">
            {depositStatus === 'PAID' ? (
              <IconWait />
            ) : (
              <IconSuccess className="confirmation__icon" />
            )}
          </div>
          {depositStatus === 'PAID' ? (
            <>
              <h4 className="confirmation__text">
                Waiting for network confirmation
              </h4>
              <button
                className="lk-currency__btn lk-currency__btn--danger"
                onClick={doCancelDeposit}
              >
                Cancel
              </button>
            </>
          ) : (
            <>
              <h4 className="confirmation__text">Deposit completed</h4>
              <button
                className="lk-currency__btn"
                onClick={() => refreshCurrentDepositQuery()}
              >
                Ok
              </button>
            </>
          )}
        </div>
      </div>
    </div>
  );
};

