import CopyToClipboard from "react-copy-to-clipboard";
import useAlertsActions from "../actions/alerts";

type CopyAreaProps = {
    text: string
    children: JSX.Element
}
const CopyArea = ({ text, children }: CopyAreaProps) => {
    const { addAlert } = useAlertsActions()
    return(
        <CopyToClipboard text={text} onCopy={() => addAlert('success', 'Copied')}>
            {children}
        </CopyToClipboard>
    )
};
export default CopyArea