// Атом для хранения информации о валидности полей во всех формах
export const depositFieldsValidity = atom({
  key: 'depositFieldsValidity',
  default: false,
});

export const depositCardsFieldsValidity = atom({
  key: 'depositCardsFieldsValidity',
  default: false,
});

export const cardPricesQuery = selector<CardPriceItem[]>({
  key: 'pricesQuery',
  get: () => pricesRequest(),
});

export const depositCurrencyState = atom({
  key: 'depositCurrencyState',
  default: 'usdt-trc',
  effects: [persistEffect('depositCurrencyState')],
});

export const depositAmountState = atom<string | undefined>({
  key: 'depositAmountState',
  // default: 0,
  effects: [persistEffect('depositAmountState')],
});

export const currentDepositQuery = selector<OngoingTransaction | undefined>({
  key: 'currentDepositQuery',
  get: async () => {
    const response = await request('/current_deposit', {
      token: 'ddbc3dd43ccf03417c7406e3db89493182821f2363cc11dec1bffadc005850e4',
    });
    const status = response?.data?.status;
    const deposit = response?.data?.deposit;
    if (deposit) {
      deposit.transaction_id = response?.data?.id as number;
      deposit.status = status as string;
    }
    // if (status !== 'FILLED') return;
    return deposit as OngoingTransaction;
  },
});
