const SpecifyAmount = () => {
  const [depositAmount, setDepositAmount] = useRecoilState(depositAmountState);

  const depositCurrency = useRecoilValue(depositCurrencyState);
  const { makeDeposit } = useDepositActions();
  const currency = useRecoilValue(depositCurrencyState);

  const validityForm = useRecoilValue(depositFieldsValidity);
  console.log('valid', validityForm);

  const continueDeposit = () => {
    if (validityForm) {
      makeDeposit();
    }
  };
  return (
    <div id="specify-amount">
      <div className="lk-step__flex">
        <span className="lk-step__descr">To pay:</span>
        <span className="lk-step__price">
          {depositAmount} {currency === 'rub' ? 'RUB' : 'USDT'}
        </span>
      </div>
      <button
        className="lk-currency__btn"
        disabled={!validityForm}
        onClick={continueDeposit}
      >
        Deposit
      </button>
    </div>
  );
};

type UserFieldProps = {
  // title: string;
  placholderValue: string | undefined;

  // onConfirm: (value: string | number | undefined) => void;
  inputType?: string;
  isRequired?: boolean;
  // hideBtnEdit?: boolean;
};

const UserField = ({
  placholderValue,
  // onConfirm,
  // title,
  inputType,
  isRequired,
}: // hideBtnEdit = false,
UserFieldProps) => {
  const calcedInputType = inputType || 'text';
  const calcedInputRequired = isRequired || false;

  const [changedValue, setChangedValue] = useState('');

  return (
    <>
      <label>
        <span className="sr-only">Введите ваш {placholderValue}</span>
        <input
          autoComplete="off"
          type={calcedInputType}
          value={changedValue}
          placeholder={placholderValue}
          required={calcedInputRequired}
          onChange={(e: React.ChangeEvent<HTMLInputElement>) =>
            setChangedValue(e.target.value)
          }
          className="user-field"
        />
      </label>
    </>
  );
};

const UserInfo = () => {
  const [depositAmount, setDepositAmount] = useRecoilState(depositAmountState);
  const [selectState, setSelectState] = useState(false);
  const prices = useRecoilValue(cardPricesQuery);
  const [validityForm, setvalidityForm] = useRecoilState(depositFieldsValidity);

  const togglerDropdown = () => {
    setSelectState(!selectState);
  };

  const [emailValue, setEmailValue] = useState('');
  const [phoneValue, setPhoneValue] = useState('');
  const [isValidEmail, setIsValidEmail] = useState(false);
  const [isValidPhone, setIsValidPhone] = useState(false);

  const validityEmail = (event: React.ChangeEvent<HTMLInputElement>) => {
    const emailRegex = /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i;
    const newEmail = event.target.value;
    setEmailValue(newEmail);
    setIsValidEmail(emailRegex.test(newEmail));
  };

  const validityTel = (event: React.ChangeEvent<HTMLInputElement>) => {
    const value = event.target.value;
    const isValid = /^\+?[0-9\s]*$/.test(value);
    setPhoneValue(value);
    setIsValidPhone(isValid);
  };

  if (isValidEmail && isValidPhone) {
    setvalidityForm(true);
  }

  return (
    <div className="user-info">
      <span className="user-info__title">For payment</span>
      <div
        className={[
          'user-info__select',
          selectState ? 'user-info__select--active' : '',
        ].join(' ')}
      >
        <div className="user-info__header" onClick={togglerDropdown}>
          <span className="user-info__price">{depositAmount}</span>
          <button
            className="user-info__btn"
            aria-label="Открыть select"
          ></button>
        </div>
        <ul className="user-info__dropdown">
          {prices.map((price) => (
            <li className="user-info__option" key={price.id}>
              <button
                className="user-info__value"
                onClick={() => {
                  setDepositAmount(price.price);
                  togglerDropdown();
                }}
              >
                {price.balance_amount}
              </button>
            </li>
          ))}
        </ul>
      </div>
      <ul className="user-info__list">
        <li className="user-info__item">
          <label>
            <span className="sr-only">Введите ваш email</span>
            <input
              autoComplete="off"
              type={'email'}
              value={emailValue}
              placeholder={'E-mail'}
              required
              onChange={validityEmail}
              className={`user-field`}
              // ${!isValidEmail && 'user-field--error'}`
            />
          </label>
        </li>
        <li className="user-info__item">
          <label>
            <span className="sr-only">Введите ваш phone</span>
            <input
              autoComplete="off"
              type={'tel'}
              value={phoneValue}
              placeholder={'Phone'}
              required
              onChange={validityTel}
              className={`user-field`}
              // ${!isValidPhone && 'user-field--error'}`
            />
          </label>
        </li>
      </ul>
      {/* <ul className="user-info__list">
        <li className="user-info__item">
          <UserField
            placholderValue={'E-mail'}
            inputType={'email'}
            isRequired={true}
          />
        </li>
        <li className="user-info__item">
          <UserField
            placholderValue={'Phone'}
            inputType={'tel'}
            isRequired={true}
          />
        </li>
      </ul> */}
      <p className="user-info__text">*For 3D Secure</p>
    </div>
  );
};

