const FiatPaymentOngoing = () => {
  const deposit = useRecoilValue(currentDepositQuery);
  const invoice = deposit?.invoice_data as InvoiceData;
  const [validityForm, setvalidityForm] = useRecoilState(
    depositCardsFieldsValidity
  );
  const { cancelDeposit, depositCardPay, confirmDeposit } = useDepositActions();
  // const invoice = { amount: 500 };
  const [pan, setPan] = useState('');
  const [panisValid, setPanisValid] = useState(false);
  const [month, setMonth] = useState('');
  const [monthIsValid, setMonthIsValid] = useState(false);
  const [year, setYear] = useState('');
  const [yearIsValid, setYearIsValid] = useState(false);
  const [cvv, setCvv] = useState('');
  const [cvvisValid, setCvvIsValid] = useState(false);

  const validityNumCard = (e: React.ChangeEvent<HTMLInputElement>) => {
    const cardNumber = e.target.value.replace(/[\s-]/g, '');
    // const inputCard = context['.pay-input-card'].elem;

    // Разделение номера карты на блоки по 4 цифры и добавление пробелов
    const formattedCardNumber = cardNumber.replace(/(.{4})/g, '$1 ').trim();
    // .trimRight();

    // Установка отформатированного номера карты в поле ввода
    setPan(formattedCardNumber);

    // Ограничение максимальной длины номера карты
    const maxLength = 16;
    if (cardNumber.length < maxLength) {
      setPanisValid(false);
      return;
    } else {
      setPan(
        cardNumber
          .slice(0, maxLength)
          .replace(/(.{4})/g, '$1 ')
          .trim()
      );
      // Разделение номера карты на блоки по 4 цифры и добавление пробелов
      // const formattedCardNumber = cardNumber.replace(/(.{4})/g, '$1 ').trim();

      // Установка отформатированного номера карты в поле ввода
      // setPan(formattedCardNumber);
    }

    setPanisValid(true);

    return;

    // const value = e.target.value;
    // let cardNumber = value.replace(/\D/g, '');

    // // Ограничиваем максимальную длину до 16 цифр
    // cardNumber = cardNumber.slice(0, 16);

    // // Разбиваем номер карты на группы по 4 цифры и добавляем пробелы между группами
    // cardNumber = cardNumber.replace(/(\d{4})/g, '$1 ');
    // cardNumber = cardNumber.trim();

    // setPan(cardNumber);
  };

  const validityCvv = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value;
    let cvc = value.replace(/\D/g, '');
    const isValidCvc = /^\d{0,4}$/.test(value);

    cvc = cvc.slice(0, 3);

    setCvv(cvc);
    setCvvIsValid(isValidCvc);
  };

  const validityMonth = (e: React.ChangeEvent<HTMLInputElement>) => {
    const monthValue = e.target.value;

    const isValid =
      /^\d{1,2}$/.test(monthValue) &&
      Number(monthValue) > 0 &&
      Number(monthValue) <= 12;

    setMonth(monthValue.slice(0, 2));
    setMonthIsValid(isValid);
  };

  const validityYear = (e: React.ChangeEvent<HTMLInputElement>) => {
    const yearValue = e.target.value;

    const currentYear = String(new Date().getFullYear()).slice(2, 4);
    const isValid =
      /^\d{1,2}$/.test(yearValue) && Number(yearValue) >= Number(currentYear);

    setYear(yearValue.slice(0, 2));
    setYearIsValid(isValid);
  };

  if (panisValid && monthIsValid && yearIsValid && cvvisValid) {
    setvalidityForm(true);
  } else {
    setvalidityForm(false);
  }
  return (
    <div className="">
      <div className="bank-card">
        <div className="bank-card__fields">
          <fieldset className="fg">
            <label>Номер карты</label>
            <input
              type="tel"
              value={pan}
              // defaultValue={pan}
              // pattern="[0-9]+"
              className="bank-card-input"
              onChange={validityNumCard}
            />
          </fieldset>
          <fieldset className="bank-card__month fg">
            <label>Месяц</label>
            <input
              type="text"
              value={month}
              onChange={validityMonth}
              className="bank-month"
            />
          </fieldset>
          <fieldset className="bank-card__year fg">
            <label>Год</label>
            <input
              type="text"
              value={year}
              onChange={validityYear}
              className="bank-year"
            />
          </fieldset>
          <fieldset className="bank-card__cvv fg">
            <label>CVV</label>
            <input
              type="text"
              value={cvv}
              onChange={validityCvv}
              className="bank-cvv"
            />
          </fieldset>
        </div>
        <div className="lk-currency__balance lk-card__info">
          <span className="lk-card__info-title">Стоимость:</span>
          {/* <span className="lk-card__info-desc">{deposit?.amount} Руб</span> */}
          <span className="lk-card__info-desc">
            {(+invoice.amount).toLocaleString('en-US', {
              maximumFractionDigits: 8,
            })}{' '}
            Руб
          </span>
        </div>
        <button
          className="lk-currency__btn"
          disabled={!validityForm}
          onClick={(e) => {
            e.preventDefault();
            depositCardPay({ pan, month, year, cvv });
            confirmDeposit();
          }}
        >
          I&apos;ve paid
        </button>
        <button
          className="lk-currency__btn lk-currency__btn--link"
          onClick={(e) => {
            e.preventDefault;
            cancelDeposit();
          }}
        >
          Cancel
        </button>
      </div>
    </div>
  );
};

const Pay = () => {
  const currentDepositQueryLoadable =
    useRecoilValueLoadable_TRANSITION_SUPPORT_UNSTABLE(currentDepositQuery);
  const currency = useRecoilValue(depositCurrencyState);
  // const amount = useRecoilValue(depositAmountState)

  console.log(currentDepositQueryLoadable);

  const Content = () => {
    // useEffect(() => {
    //   if (currency) moveToSpecifyAmount();
    // });
    const deposit = currentDepositQueryLoadable.contents;

    // 1 - choose currency
    // 2 - specify amount
    // 3 - pay invoice
    const calculateCurrentStep = () => {
      if (deposit) {
        const dep = deposit as OngoingTransaction;
        switch (dep.status) {
          case 'FILLED':
            return currency !== 'rub' ? 3 : 4;
          case 'PAID':
            return 5;
        }
      }
      return currency ? 2 : 1;
    };
    const currentStep = calculateCurrentStep();
    console.log(currency);
    console.log(currentStep);
    return (
      <div className="lk-step lk__block lk__block--border">
        {/* <Timeline value={currentStep} /> */}
        {/* <h2>Service currently unable. Our team fixing it ASAP</h2> */}

        {/* <ConfirmStep /> */}
        {/* TODO: Delete this and uncomment next when flow will be done*/}
        {currentStep === 5 && <Refresher />}
        {currentStep === 3 && <CrpyptoPaymentOngoing />}
        {currentStep === 4 && <FiatPaymentOngoing />}
        {currentStep <= 2 && <UserInfo />}
        {currentStep <= 2 && <PaymentMethods />}
        {currentStep <= 2 && <SpecifyAmount />}
      </div>
    );
  };
  switch (currentDepositQueryLoadable.state) {
    case 'hasValue':
      return <Content />;
    case 'loading':
      return <div className="lk__block">Loading</div>;
    case 'hasError':
      return <div className="lk__block">Error</div>;
  }
};

const Deposit = () => <Pay />;

export default Deposit;
