import { atom, selector, selectorFamily } from 'recoil';
import { ProfileState, Transaction } from '@/types';
import { authRequest } from '@/utils/api';

import { tokenState } from './auth';

export const profileState = atom<ProfileState>({
  key: 'profile',
  default: { issued_cards: [] },
});

// TODO Refactor periodicaly updater

export const profileQuery = selector<ProfileState>({
  key: 'profileQuery',
  get: async ({ get }) => authRequest('/profile', get(tokenState) as string),
});

