export const transactionsState = atom<Transaction[]>({
  key: 'transactions',
  default: [],
});

export const transactionsQuery = selector<Transaction[]>({
  key: 'transactionsQuery',
  get: async ({ get }) => {
    const result = await authRequest(
      '/transactions',
      get(tokenState) as string
    );
    return result.transactions;
  },
});

export const selectCard = selectorFamily({
  key: 'selectCard',
  get:
    (cardId: string) =>
    ({ get }) => {
      const { issued_cards: cards } = get<ProfileState>(profileQuery);
      return cards.find((card) => card.id === cardId);
    },
});

export const statementQuery = selectorFamily({
  key: 'statementQuery',
  get:
    (cardId: string) =>
    async ({ get }) => {
      const {
        statement: { data },
      } = await authRequest('/statements', get(tokenState) as string, {
        id: cardId,
      });
      return data;
    },
});
