import { differenceInSeconds } from 'date-fns';
import { useRecoilState } from 'recoil';
import { alertsState } from '@/store/alerts';

const useAlertsActions = () => {
  const [alerts, setAlets] = useRecoilState(alertsState);

  const cleanupAlerts = () => {
    const actualAlerts = alerts.filter(
      (alert) => differenceInSeconds(new Date(), alert.dateAdded) < 3
    );
    setAlets(actualAlerts);
  };

  const addAlert = (type: 'success' | 'error', message: string) => {
    const id = Math.random().toString(36).slice(2, 20);
    const dateAdded = new Date();
    setAlets([...alerts, { id, type, message, dateAdded }]);
    setTimeout(() => cleanupAlerts(), 3000);
  };

  const closeAlert = (id: string) => {
    setAlets(alerts.filter((alert) => alert.id !== id));
  };

  return { addAlert, closeAlert, cleanupAlerts };
};

export default useAlertsActions;
