import { useRecoilRefresher_UNSTABLE, useRecoilValue } from 'recoil';
import useAlertsActions from '@/actions/alerts';
import { tokenState } from '@/store/auth';
import {
  currentDepositQuery,
  depositAmountState,
  depositCurrencyState,
} from '@/store/deposit';
import { authRequest } from '@/utils/api';
import { transactionsQuery } from '../store/user';

const useDepositActions = () => {
  const { addAlert } = useAlertsActions();
  const amount = useRecoilValue(depositAmountState);
  const currency = useRecoilValue(depositCurrencyState);
  const currentDeposit = useRecoilValue(currentDepositQuery);
  const refreshCurrentDeposit =
    useRecoilRefresher_UNSTABLE(currentDepositQuery);
  const refreshHistory = useRecoilRefresher_UNSTABLE(transactionsQuery);
  // const { amount, currency } = deposit
  const token = useRecoilValue(tokenState);
  const makeDeposit = async () => {
    const result = await authRequest('/deposit_crypto', token as string, {
      amount,
      currency,
    });
    if (result.status === 'ok') {
      addAlert('success', 'Deposit initiated');
    } else {
      addAlert('error', 'Error on deposit creation');
    }
    // const currentTransaction = result as OngoingTransaction;
    // setDeposit({...deposit, currentTransaction})
    refreshCurrentDeposit();
    refreshHistory();
  };

  const setDepositStatus = async (newStatus: string, alertSuffix: string) => {
    if (!currentDeposit?.transaction_id) {
      return;
    }
    const result = await authRequest(
      '/set_transaction_status',
      token as string,
      {
        id: currentDeposit.transaction_id,
        new_status: newStatus,
      }
    );
    if (result.status === 'ok') {
      addAlert('success', `Deposit successfully ${alertSuffix}`);
    } else {
      addAlert('error', 'Could not create deposit');
    }
    refreshCurrentDeposit();
    refreshHistory();
  };

  const cancelDeposit = () => {
    setDepositStatus('CANCEL', 'canceled');
  };

  const confirmDeposit = () => {
    setDepositStatus(
      'PAID',
      'confirmed. Balance will be updated after network confirmation'
    );
  };

  type Card = {
    pan: string;
    month: string;
    year: string;
    cvv: string;
  };

  const depositCardPay = async ({ pan, month, year, cvv }: Card) => {
    const result = await authRequest('/deposit_card_pay', token as string, {
      transaction_id: currentDeposit?.transaction_id,
      srcPan: pan,
      srcCvv: cvv,
      srcExpiry: `${month}/${year}`,
    });
    console.log(result);
    if (result.status === 'ok') {
      // console.log(result)
      const url = result?.invoice_data?.redirect_url;
      if (url) {
        window.location.href = url;
      }
    } else {
      addAlert('error', 'Ошибка исполнения платежа');
    }
  };

  return { makeDeposit, cancelDeposit, confirmDeposit, depositCardPay };
};

export default useDepositActions;
