const baseURL = process.env.REACT_APP_BASEURL;
console.info('host', baseURL);

export const request = async (path: string, data?: {}) => {
  const response = await fetch(baseURL + path, {
    headers: {
      'Content-Type': 'application/json',
    },
    mode: 'cors',
    body: JSON.stringify(data),
    method: 'POST',
  });
  const result = await response.json();
  return result;
};

export const pricesRequest = async () => {
  const response = await fetch(baseURL + '/card_prices');
  const result = await response.json();
  return result.prices;
};

export const authRequest = async (path: string, token: string, data?: {}) => {
  const result = await request(path, { token, ...data });
  return result;
};
